<template>
  <div>
    <router-link to="newsection">
      <a-button class="editable-add-btn" type="primary">新增</a-button>
    </router-link>
    <a-table :columns="columns" :dataSource="data" bordered>
      <template
        v-for="col in ['columnlevel']"
        :slot="col"
        slot-scope="text, record, index"
      >
        <div :key="col">
          <a-input
            v-if="record.editable"
            style="margin: -5px 0"
            :value="text"
            @change="e => handleChange(e.target.value, index, col)"
          />
          <template v-else>{{text}}</template>
        </div>
      </template>
      <template slot="operation" slot-scope="text, record, index">
        <div class="editable-row-operations">
          <span v-if="record.editable">
            <a @click="() => save(index)">保存</a>
            <a-popconfirm title="Sure to cancel?" @confirm="() => cancel(index)">
              <a>取消</a>
            </a-popconfirm>
          </span>
          <span v-else>
            <a @click="() => edit(record,index)">修改</a>
          </span>
          <span>
            <a @click="handleDelete(record,index)">删除</a>
          </span>
        </div>
      </template>
    </a-table>
  </div>
</template>
<script>
import { deletenewitem, updatanewitem, getallnewcolumnlist } from "@/api/news";
const columns = [
  {
    title: "级别",
    dataIndex: "levels",
    scopedSlots: { customRender: "levels" }
  },
  {
    title: "栏目",
    dataIndex: "columnlevel",
    scopedSlots: { customRender: "columnlevel" }
  },
  {
    title: "操作",
    dataIndex: "operation",
    scopedSlots: { customRender: "operation" }
  }
];
export default {
  data() {
    return {
      data: [],
      cacheData: [],
      columns
    };
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      getallnewcolumnlist().then(res => {
        let { data } = res;
        if (data.code == 200) {
          this.data = data.data;
          this.cacheData = this.data.map(item => ({ ...item }));
          // console.log("接受的数据", this.cacheData);
        }
      });
    },
    handleDelete(params, index) {
      deletenewitem({ column: params.columnlevel }).then(res => {
        this.init();
        // console.log("删除的", res);
      });
    },
    handleChange(value, index, column) {
      // console.log(value, index, column);
      const newData = [...this.data];
      const target = newData.filter((item, _index) => _index === index)[0];
      if (target) {
        target[column] = value;
        this.data = newData;
      }
    },
    edit(par, index) {
      const newData = [...this.data];
      const target = newData.filter((item, _index) => _index === index)[0];
      if (target) {
        target.editable = true;
        this.data = newData;
      }
    },
    save(index) {
      const newData = [...this.data];
      const target = newData.filter((item, _index) => _index === index)[0];
      // console.log("保存", target);
      const oldval = this.cacheData.filter((item,_index) =>_index === index)[0]
      // console.log('老的值',oldval)
      if (target) {
        delete target.editable;
        this.data = newData;
        this.cacheData = newData.map(item => ({ ...item }));
        updatanewitem({oldval:oldval.columnlevel,newval:target.columnlevel}).then(res =>{
          // console.log('更新后的',res)
        })
      }
    },
    cancel(index) {
      const newData = [...this.data];
      const target = newData.filter((item, _index) => _index === index)[0];
      if (target) {
        Object.assign(
          target,
          this.cacheData.filter((item, _index) => _index === index)[0]
        );
        delete target.editable;
        this.data = newData;
      }
    }
  }
};
</script>
<style scoped>
.editable-row-operations a {
  margin-right: 8px;
}
</style>



